
import axios from 'axios';
import actions from './actions';

axios.defaults.baseURL = 'http://localhost:3000/goit-react-hw-07-phonebook';

const fetchContacts = () => async dispatch => {
  axios
   .get('/contacts')
   .then(({ data }) => {console.log(data);dispatch(actions.fetchContactsSuccess(data))})
    .catch(error => dispatch(actions.fetchContactsError(error)));

  // axios
  //   .get('/todos')
  //   .then(({ data }) => dispatch(fetchTodosSuccess(data)))
  //   .catch(error => dispatch(fetchTodosError(error)));
};



export default fetchContacts;
